const apiGatewayUrl = 'http://localhost:8083';

export const login = async ({ email, password }) => {
  try {
    const response = await fetch(`${apiGatewayUrl}/auth/generateToken`, {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
      },
      body: JSON.stringify({ email, password }),
    });

    if (response.ok) {
      const token = await response.text();
      localStorage.setItem('token', token); // Save the token in localStorage
      console.log('Login successful:', token);
      return token;
    } else {
      const errorText = await response.json(); // Parse JSON response
      console.error('Login failed:', errorText);

      if (response.status === 403 && errorText && errorText.error === 'Forbidden') {
        // Extract error message from JSON response
        throw new Error('Invalid login credentials');
      } else {
        throw new Error('Login failed');
      }
    }
  } catch (error) {
    console.error('Error during login:', error);
    throw error;
  }
};

export const register = async (name, email, password, roles) => {
  try {
    const response = await fetch(`${apiGatewayUrl}/auth/addNewUser`, {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
      },
      body: JSON.stringify({ name, email, password, roles }),
    });

    if (response.ok) {
      const data = await response.text();
      return data;
    } else {
      throw new Error('Registration failed');
    }
  } catch (error) {
    console.error('Error during registration:', error.message);
    throw error;
  }
};
