import React, { useState, useEffect, useRef } from 'react';
import axios from 'axios';
import { Pie } from 'react-chartjs-2';
import { ArcElement } from 'chart.js';
import Chart from 'chart.js/auto';
import { useNavigate } from 'react-router-dom';

import './UserProfile.css';

const apiGatewayUrl = 'http://localhost:8083';

const categories = [
  'Groceries',
  'Utilities',
  'Entertainment',
  'Transportation',
  'Dining',
  'Health',
  'Education',
  'Personal Care',
  'Gifts',
  'Miscellaneous',
];

const subcategories = {
  Groceries: ['Fruits', 'Vegetables', 'Dairy', 'Bakery', 'Beverages'],
  Utilities: ['Electricity', 'Water', 'Gas', 'Internet', 'Phone'],
  Entertainment: ['Movies', 'Books', 'Music', 'Games', 'Concerts'],
  Transportation: ['Fuel', 'Public Transit', 'Parking', 'Maintenance', 'Tolls'],
  Dining: ['Restaurants', 'Fast Food', 'Coffee Shops', 'Takeout', 'Delivery'],
  Health: ['Pharmacy', 'Doctor Visits', 'Fitness', 'Insurance', 'Medications'],
  Education: ['Tuition', 'Books', 'Supplies', 'Courses', 'Online Learning'],
  'Personal Care': ['Clothing', 'Haircuts', 'Beauty Products', 'Gym Memberships', 'Spa Services'],
  Gifts: ['Birthdays', 'Holidays', 'Special Occasions', 'Weddings', 'Anniversaries'],
  Miscellaneous: ['Home Maintenance', 'Pet Expenses', 'Subscriptions', 'Travel', 'Charitable Donations'],
};

const UserProfile = () => {
  const navigate = useNavigate();
  const isAuthenticated = localStorage.getItem('token');
  const chartRef = useRef(null);
  const [loading, setLoading] = useState(true);
  const [submitSuccess, setSubmitSuccess] = useState(false);
  const [expenseData, setExpenseData] = useState({
    date: '',
    amount: 0,
    category: 'Groceries',
    subcategory: 'Fruits',
    notes: '',
  });

  const [filterData, setFilterData] = useState({
    category: '',  // Change from categoryFilter to category
    subcategory: '',  // Change from subcategoryFilter to subcategory
    year: '',
    month: '',
    week: '',
  });
  
  
  const [expenses, setExpenses] = useState([]);
  const [months, setMonths] = useState([]);
  const [weeks, setWeeks] = useState([]);
  const [yearOptions, setYearOptions] = useState([]);
  const [chartData, setChartData] = useState({
    labels: [],
    datasets: [
      {
        data: [],
        backgroundColor: [
          '#FF6384',
          '#36A2EB',
          '#FFCE56',
          '#4CAF50',
          '#FF9800',
          '#9C27B0',
          '#607D8B',
          '#2196F3',
          '#FF5722',
          '#795548',
        ],
        hoverBackgroundColor: [
          '#FF6384',
          '#36A2EB',
          '#FFCE56',
          '#4CAF50',
          '#FF9800',
          '#9C27B0',
          '#607D8B',
          '#2196F3',
          '#FF5722',
          '#795548',
        ],
      },
    ],
  });

  useEffect(() => {
    // Redirect to the login page if the user is not authenticated
    if (!isAuthenticated) {
      navigate('/login');
    } else {
      // Fetch data only if the user is authenticated
      fetchData();
    }
  }, [isAuthenticated, navigate]);

  // Initialize month and week options
// Initialize month and week options
useEffect(() => {
  const calculateWeeksInMonth = (year, month) => {
    const firstDay = new Date(year, month - 1, 1);
    const lastDay = new Date(year, month, 0);
    const daysInMonth = lastDay.getDate();
    const daysInFirstWeek = 7 - firstDay.getDay();
    const remainingDays = daysInMonth - daysInFirstWeek;
    const fullWeeks = Math.ceil(remainingDays / 7) + 1;
    return Array.from({ length: fullWeeks }, (_, i) => i + 1);
  };

  const yearOptions = Array.from({ length: 10 }, (_, i) => new Date().getFullYear() - i);
  setYearOptions(yearOptions);

  const monthOptions = Array.from({ length: 12 }, (_, i) => i + 1);
  setMonths(monthOptions);

  setWeeks(calculateWeeksInMonth(filterData.year, filterData.month));  // Use filterData instead of filterData.yearFilter and filterData.monthFilter
}, [filterData.year, filterData.month]);  // Update dependencies to include filterData.year and filterData.month


  // Check if a token exists in localStorage
  useEffect(() => {
    const checkToken = async () => {
      try {
        const token = localStorage.getItem('token');

        if (!token) {
          console.error('No token found in localStorage.');
        } else {
          setLoading(false);
        }
      } catch (error) {
        console.error('Error checking token:', error.message);
        setLoading(false);
      }
    };

    checkToken();
  }, []);

  // Fetch data from the server based on filters
  useEffect(() => {
   // fetchData();
  }, [filterData]);

  // Update chart data when expenses change
  useEffect(() => {
    updateChartData();
  }, [expenses]);

  // Handle changes in filter inputs
  const handleFilterChange = (e) => {
    const { name, value } = e.target;
    setFilterData((prevData) => ({
      ...prevData,
      [name]: value,
    }));
  };
  
  

  // Handle changes in expense form inputs
  const handleExpenseChange = (e) => {
    const { name, value } = e.target;
    setExpenseData((prevData) => ({
      ...prevData,
      [name]: value,
    }));
  };

  // Handle submission of the expense form
  const handleExpenseSubmit = async (e) => {
    e.preventDefault();
    console.log('handleExpenseSubmit function called');

    try {
      const token = localStorage.getItem('token');

      console.log('Request data:', expenseData);

      const addExpenseUrl = `${apiGatewayUrl}/api/expenses/add`;
      console.log('Constructed URL:', addExpenseUrl);

      await axios.post(
        `${apiGatewayUrl}/api/expenses/add`,
        {
          ...expenseData,
        },
        {
          headers: {
            Authorization: `Bearer ${token}`,
          },
        }
      );

      console.log('Expense created successfully!');
      setSubmitSuccess(true);

      setExpenseData({
        date: '',
        amount: 0,
        category: '',
        subcategory: '',
        notes: '',
      });
  
      //fetchData();
    } catch (error) {
      console.error('Error creating expense:', error);
    }
  };

  // Fetch data from the server based on filters
  const fetchData = async () => {
    try {
      const token = localStorage.getItem('token');
      console.log('Request data:', filterData);

      const { category, subcategory, year, month, week } = filterData;

      console.log('Request data:', filterData);
      const response = await axios.get(`${apiGatewayUrl}/api/expenses/user/getFilterExpense`, {
        headers: {
          Authorization: `Bearer ${token}`,
        },
        params: { category, subcategory, year, month, week },
            });

      setExpenses(response.data);
    } catch (error) {
      console.error('Error fetching expenses:', error);
    }
  };

  // Update chart data based on the fetched expenses
  const updateChartData = () => {
    const categoryCounts = {};
    expenses.forEach((expense) => {
      const category = expense.category;
      categoryCounts[category] = (categoryCounts[category] || 0) + 1;
    });

    const chartLabels = Object.keys(categoryCounts);
    const chartDataValues = Object.values(categoryCounts);

    setChartData({
      labels: chartLabels,
      datasets: [
        {
          data: chartDataValues,
          backgroundColor: chartData.datasets[0].backgroundColor,
          hoverBackgroundColor: chartData.datasets[0].hoverBackgroundColor,
        },
      ],
    });
  };

  return (
    <div className="user-profile-container">
      <div className="left-side">
        <h2>Filter Expenses</h2>
        <form onSubmit={(e) => { e.preventDefault(); fetchData(); }}>
          <label>Category:</label>
          <select name="category" value={filterData.category} onChange={handleFilterChange}>
            {categories.map((category) => (
              <option key={category} value={category}>
                {category}
              </option>
            ))}
          </select>

          <label>Subcategory:</label>
          <select
            name="subcategory"
            value={filterData.subcategory}
            onChange={handleFilterChange}
          >
            {subcategories[filterData.category] &&
              subcategories[filterData.category].map((subcategory, index) => (
                <option key={subcategory} value={subcategory}>
                  {subcategory}
                </option>
              ))}
          </select>

          <label>Year:</label>
          <select name="year" value={filterData.year} onChange={handleFilterChange}>
            <option value="">Select Year</option>
            {yearOptions.map((year) => (
              <option key={year} value={year}>
                {year}
              </option>
            ))}
          </select>

          <label>Month:</label>
          <select name="month" value={filterData.month} onChange={handleFilterChange}>
            <option value="">Select Month</option>
            {months.map((month) => (
              <option key={month} value={month}>
                {month}
              </option>
            ))}
          </select>

          <label>Week:</label>
          <select name="week" value={filterData.week} onChange={handleFilterChange}>
            <option value="">Select Week</option>
            {weeks.map((week) => (
              <option key={week} value={week}>
                {week}
              </option>
            ))}
          </select>

        <button type="submit">Apply Filters</button>
      </form>

          

        <h2>Filtered Expenses</h2>
        <table>
          <thead>
            <tr>
              <th>ID</th>
              <th>Date</th>
              <th>Amount</th>
              <th>Category</th>
              <th>Subcategory</th>
              <th>Notes</th>
            </tr>
          </thead>
          <tbody>
            {expenses.map((expense) => (
              <tr key={expense.id}>
                <td>{expense.id}</td>
                <td>{expense.date}</td>
                <td>{expense.amount}</td>
                <td>{expense.category}</td>
                <td>{expense.subcategory}</td>
                <td>{expense.notes}</td>
              </tr>
            ))}
          </tbody>
        </table>

        <h2>Expense Categories Percentage</h2>
        <Pie data={chartData} />
      </div>

      <div className="right-side">
        <h2>User Profile</h2>
        {loading ? (
          <p>Loading user details...</p>
        ) : (
          <div className="expense-form">
            <h3>Expense Form</h3>
            
            <form onSubmit={handleExpenseSubmit}>
              <div>
                <label>Date:</label>
                <input type="text" name="date" value={expenseData.date} onChange={handleExpenseChange} />
              </div>
              <div>
                <label>Amount:</label>
                <input
                  type="number"
                  name="amount"
                  value={expenseData.amount}
                  onChange={handleExpenseChange}
                />
              </div>
              <div>
                <label>Category:</label>
                <select
                  name="category"
                  value={expenseData.category}
                  onChange={handleExpenseChange}
                >
                  {categories.map((category) => (
                    <option key={category} value={category}>
                      {category}
                    </option>
                  ))}
                </select>
              </div>
              <div>
                <label>Subcategory:</label>
                <select
                  name="subcategory"
                  value={expenseData.subcategory}
                  onChange={handleExpenseChange}
                >
                  {subcategories[expenseData.category] ? (
                    subcategories[expenseData.category].map((subcategory, index) => (
                      <option key={subcategory} value={subcategory}>
                        {subcategory}
                      </option>
                    ))
                  ) : (
                    <option value="">Select a category first</option>
                  )}
                </select>
              </div>
              <div>
                <label>Notes:</label>
                <input type="text" name="notes" value={expenseData.notes} onChange={handleExpenseChange} />
              </div>
              <button type="submit">Add Expense</button>
            </form>
            {submitSuccess && (
            <p className="success-message">Expense Submitted Successfully!</p>
            )}
          </div>
        )}
      </div>
    </div>
  );
};

export default UserProfile;
