import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import { login } from '../../../services/authService';
import './Login.css';

const Login = ({ onLogin }) => {
  const [username, setUsername] = useState('');
  const [password, setPassword] = useState('');
  const [errorMessage, setErrorMessage] = useState(''); // New state for error message
  const navigate = useNavigate();

  const handleLogin = async (e) => {
    e.preventDefault();
    try {
      const token = await login({ email: username, password });
      console.log('Login successful:', token);

      // Handle token and set user state if needed

      // Redirect to the desired page
      navigate('/user/userProfile'); // or '/admin/adminProfile' based on user roles
    } catch (error) {
      console.error('Error during login:', error.message);

      if (error.message === 'Invalid login credentials') {
        setErrorMessage('Invalid login credentials');
      } else {
        setErrorMessage('An error occurred during login'); // Generic error message
      }
    }
  };

  return (
    <div className="login-container">
      <h2>Login</h2>

  


      <form onSubmit={handleLogin}>
        <div className="form-group">
          <label>Email:</label>
          <input type="text" value={username} onChange={(e) => setUsername(e.target.value)} />
        </div>
        <div className="form-group">
          <label>Password:</label>
          <input type="password" value={password} onChange={(e) => setPassword(e.target.value)} />
        </div>
        <button type="submit" className="login-button">
          Login
        </button>
      </form>

      
      {errorMessage && (
        <div className="error-message" style={{ color: 'red' }}>
          {errorMessage}
        </div>
      )}
    </div>
  );
};

export default Login;
