// src/components/Home/Home.js

import React from 'react';
import { Link } from 'react-router-dom';
import './Home.css';

const Home = () => (
  
    
  <div className="home-container">
    
    

    <section className="intro-section">
      <h1>Welcome to Your Expense Management App</h1>
      <p>
        An all-in-one solution for managing your expenses, collaborating with others, and gaining valuable insights.
      </p>
      <Link to="/register">
        <button>Get Started</button>
      </Link>
    </section>

    <section className="expense-section" id="expense-management">
  <h2>Expense Management</h2>
  <p>
    Simplify your financial journey with BudgetBotique.
  </p>
  <h3>Key Features:</h3>
  <div className="key-features">
    <div className="key-feature-box">
      <h4>Expense Tracking</h4>
      <p>Effortlessly track and categorize your expenses</p>
    </div>
    <div className="key-feature-box">
      <h4>Automated Expense Reports</h4>
      <p>Generate accurate and detailed expense reports with a click</p>
    </div>
    <div className="key-feature-box">
      <h4>Receipt Scanning</h4>
      <p>Scan and store receipts for easy expense management</p>
    </div>
    <div className="key-feature-box">
      <h4>Integration with Accounting Software</h4>
      <p>Seamlessly sync your expenses with popular accounting software</p>
    </div>
  </div>
  </section>
  
  <section className="pricing-section" id="pricing">
      <div className="pricing-intro">
        <h2>Choose the Right Plan for You</h2>
        <p>Manage your expenses efficiently with our flexible pricing options</p>
      </div>

      <div className="pricing-box free">
        <h3>FREE</h3>
        <p>A free plan for basic expense management</p>
        <div className="price">$0</div>
        <ul>
          <li>Track and categorize expenses</li>
          <li>Generate expense reports</li>
          <li>Set spending limits</li>
          <li>Receive email notifications</li>
        </ul>
        <Link to="/register" className="cta-button">Continue with Free</Link>
      </div>

      <div className="pricing-box basic">
        <h3>BASIC</h3>
        <p>An affordable plan with additional features</p>
        <div className="price">$9.99 / month</div>
        <ul>
          <li>All features of FREE plan</li>
          <li>Attach receipts to expenses</li>
          <li>Sync with bank accounts</li>
          <li>Export data to CSV</li>
        </ul>
        <Link to="/register" className="cta-button">Try the Basic plan</Link>
      </div>

      <div className="pricing-box pro">
        <h3>PRO</h3>
        <p>A comprehensive plan for advanced expense management</p>
        <div className="price">$19.99 / month</div>
        <ul>
          <li>All features of BASIC plan</li>
          <li>Create custom expense categories</li>
          <li>Integrate with accounting software</li>
          <li>Access priority customer support</li>
        </ul>
        <Link to="/register" className="cta-button">Try the PRO plan</Link>
      </div>
    </section>
  <section className="HowItWork-section" id="how-it-work">
  <h3>How It Works:</h3>
  <p>
    Streamline your financial activities by logging in and taking control of your expenses. Say goodbye to spreadsheets and paper receipts, and say hello to a streamlined and efficient way of managing your expenses.
     Our application ensures the highest level of security for your data.
  </p>
  </section>
  



    <section className="microservice-section" id="reporting">
      {/* Similar sections for other microservices */}
    </section>

    <section className="microservice-section" id="user-management">
      {/* ... */}
    </section>

    <section className="microservice-section" id="notification">
      {/* ... */}
    </section>

    <section className="microservice-section" id="collaboration">
      {/* ... */}
    </section>

    <section className="microservice-section" id="analytics">
      {/* ... */}
    </section>

    <section className="microservice-section" id="community">
      {/* ... */}
    </section>

    <section className="cta-section">
      <h2>Ready to Take Control of Your Finances?</h2>
      <Link to="/register">
        <button>Sign Up Now</button>
      </Link>
    </section>

    <footer className="app-footer">
      <div className="copyright-text">
        © 2024 BudgetBotique, All Rights Reserved.
      </div>
      <div className="social-icons">
        <a href="#" target="_blank" rel="noopener noreferrer">
          {/* You can use social media icons or appropriate symbols here */}
          Instagram
        </a>
        <a href="#" target="_blank" rel="noopener noreferrer">
          Facebook
        </a>
        <a href="#" target="_blank" rel="noopener noreferrer">
          Twitter
        </a>
      </div>
    </footer>
  </div>
);

export default Home;
