// src/components/Auth/Register/Register.js

import React, { useState } from 'react';
import Avatar from '@mui/material/Avatar';
import Typography from '@mui/material/Typography';
import TextField from '@mui/material/TextField';
import Button from '@mui/material/Button';
import Grid from '@mui/material/Grid';
import Link from '@mui/material/Link';
import Container from '@mui/material/Container';
import { useNavigate } from 'react-router-dom';
import './Register.css';

const Register = ({ onRegister }) => {
  const [name, setName] = useState('');
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const [showSuccessMessage, setShowSuccessMessage] = useState(false);
  const [emailError, setEmailError] = useState('');
  const navigate = useNavigate();

  const handleRegister = async (e) => {
    e.preventDefault();

    const roles = 'ROLE_USER';

    // Validate email format
    const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
    if (!emailRegex.test(email)) {
      setEmailError('Enter a valid email');
      return;
    }

    try {
      console.log('Handle register function is executing.');
      console.log('Registration data:', { name, email, password, roles });

      await onRegister(name, email, password, roles);

      setName('');
      setEmail('');
      setPassword('');

      setShowSuccessMessage(true);
      console.log('Success message displayed');
    } catch (error) {
      console.error('Error during registration:', error.message);
    }
  };

  const handleOkButtonClick = () => {
    setShowSuccessMessage(false);
    navigate('/login');
  };

  const handleEmailChange = (e) => {
    setEmail(e.target.value);
    setEmailError(''); // Clear email error message when the email input changes
  };

  return (
    <Container component="main" maxWidth="xs">
      <div className="signup-container">
        <Avatar />
        <Typography component="h1" variant="h5">
          Sign up
        </Typography>
        <form onSubmit={handleRegister} className="signup-form">
          <TextField
            autoComplete="given-name"
            name="name"
            required
            fullWidth
            id="name"
            label="Name"
            autoFocus
            value={name}
            onChange={(e) => setName(e.target.value)}
          />
          <TextField
            required
            fullWidth
            id="email"
            label="Email Address"
            name="email"
            autoComplete="email"
            value={email}
            onChange={handleEmailChange}
          />
          {emailError && <p className="error-message">{emailError}</p>}
          <TextField
            required
            fullWidth
            name="password"
            label="Password"
            type="password"
            id="password"
            autoComplete="new-password"
            value={password}
            onChange={(e) => setPassword(e.target.value)}
          />
          <Button
            type="submit"
            fullWidth
            variant="contained"
            sx={{ mt: 3, mb: 2 }}
          >
            Sign Up
          </Button>

          {showSuccessMessage && (
            <div className="success-message">
              <p>Registration successful and now you can login</p>
              <Button onClick={handleOkButtonClick} color="primary" variant="contained">
                Login
              </Button>
            </div>
          )}

          <Grid container justifyContent="flex-end">
            <Grid item>
              <Link href="/login" variant="body2">
                Already have an account? Sign in
              </Link>
            </Grid>
          </Grid>
        </form>
      </div>
    </Container>
  );
};

export default Register;
