// src/App.js

import React from 'react';
import { Routes, Route } from 'react-router-dom';
import { login, register } from './services/authService';
import { useNavigate } from 'react-router-dom';
import Register from './components/Auth/Register/Register';
import UserProfile from './components/UserProfile/UserProfile';
import AdminProfile from './components/AdminProfile/AdminProfile';
import Login from './components/Auth/Login/Login';
import Home from './components/Home/Home';
import Header from './components/Header/Header';

import './App.css';

const App = () => {
  const [user, setUser] = React.useState(null);
  const navigate = useNavigate();

  const handleLogin = async (username, password) => {
    try {
      const token = await login({ email: username, password });
      console.log('Login successful:', token);

      // Handle token, set user state if needed

      // Redirect to the user profile page
      navigate('/user/userProfile');
    } catch (error) {
      console.error('Error during login:', error.message);
      // Handle login errors
    }
  };

  const handleRegister = async (name, email, password, roles) => {
    try {
      const message = await register(name, email, password, roles);
      console.log('Registration successful:', message);
    } catch (error) {
      console.error('Error during registration:', error.message);
    }
  };

  return (
    <div>
      <Header />

      <Routes>
        <Route path="/" element={<Home />} />
        <Route path="/register" element={<Register onRegister={handleRegister} />} />
        <Route path="/user/userProfile" element={<UserProfile user={user} />} />
        <Route path="/login" element={<Login onLogin={handleLogin} />} />
      </Routes>
    </div>
  );
};

export default App;
